export function getTablecolumns() {
  return [
    {
      width: 60,
      titleAlign: "center",
      columnAlign: "center",
      type: "selection"
    },
    {
      field: "number",
      title: "",
      width: 80,
      titleAlign: "center",
      columnAlign: "center",
      isResize: true
    },
    {
      field: "name",
      title: "",
      width: 80,
      titleAlign: "center",
      columnAlign: "center",
      isResize: true
    },
    {
      field: "tel",
      title: "",
      width: 150,
      titleAlign: "center",
      columnAlign: "center",
      isResize: true
    },
    {
      field: "hobby",
      title: "",
      width: 150,
      titleAlign: "center",
      columnAlign: "center",
      isResize: true
    },
    {
      field: "address",
      title: "",
      width: 280,
      titleAlign: "center",
      columnAlign: "left",
      isResize: true
    }
  ];
}

export function getMockData() {
  let tableData = [];
  for (let i = 1; i <= 200; i++) {
    tableData.push({
      number: i,
      name: `${i}`,
      tel: parseInt(15000000000 + Math.random() * 1000000000),
      hobby: "",
      address: "115",
    });
  }
  return tableData;
}



